body {
  overflow: hidden;
  background: rgb(244, 244, 244);
}

/* width */
::-webkit-scrollbar {
  width: 0px;
  height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #c9c9c9;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

:root {
  --web-page-width: 750px;
  --primary: rgb(52, 162, 50);
}

button {
  cursor: pointer;
}

.App {
  width: 100%;
}

.App.web {
  max-width: var(--web-page-width);
  margin: 0 auto;
}

.main-size {
  display: block;
  width: 100%;
}

.main-size.web {
  max-width: var(--web-page-width);
  margin: 0 auto;
}

.main-size.hide{
  display: none;
}

#status-bar-fake {
  height: 24px;
  background: white;
}

.App-header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: calc(100% - 20px);
  height: 60px;
  padding: 0 10px;
  box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
  background: white;
}

.header-item {
  margin-bottom: 12px;
}

#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-content {
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 60px;
  padding: 10px;
  overflow-y: auto;
}

.main-content.web {
  top: 88px;
  bottom: 0;
}

.main-content.mobile-scroll {
  top: 84px;
}

.bottom-navigation {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.bottom-navigation-container {
  display: flex;
  height: 60px;
  margin: 0 5px;
  align-items: center;
  border-top: 1px solid rgb(215, 215, 215);
  background: white;
}

.bottom-navigation.hide {
  display: none;
}

.nav-btn {
  width: 25%;
  height: 60px;
  border: none;
  background: white;
}

.nav-btn.active {
  border-radius: 10px;
  height: 50px;
  color: white;
  background: var(--primary);
}

.nav-btn-text{
  font-size: 10px;
}

.scroll-content {
  display: flex;
  overflow-x: auto;
}

.flex-content {
  display: flex;
  flex-wrap: wrap;
}

#logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#app-logo {
  width: 110px;
  height: 110px;
  border-radius: 20px;
}

#app-title {
  width: 100%;
  font-size: 20px;
  text-align: center;
  /* text-align: right; */
  padding: 15px 5px;
}

/* CARD */

.card {
  position: relative;
  margin: 5px;
  overflow: hidden;
  border-radius: 10px;
  border: 1px solid rgb(218, 218, 218);
  background: rgb(255, 255, 255);
}

.card-discount {
  position: absolute;
  color: white;
  padding: 3px 4px;
  font-weight: bold;
  border-radius: 0 0 10px 0;
  background: red;
}

.card-img {
  width: 100%;
  height: 170px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0% 20%;
}

.card-content {
  padding: 0px 10px 10px;
}

.card-name {
  font-size: 14px;
  font-weight: bold;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 35px;
  margin: 10px 0 5px;
}

.card-category {
  font-size: 12px;
  padding: 7px 0;
}

.card-price-content {
  display: flex;
}

.card-price {
  font-size: 15px;
  font-weight: 600;
  padding-right: 5px;
}

.card-price.old {
  text-decoration: line-through;
}

.card-button {
  cursor: pointer;
  width: 100%;
  padding: 10px 0;
  margin-top: 10px;
  color: white;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  background: var(--primary);
}

.card-button:hover {
  background: rgb(7, 161, 20);
}

.card-button.clear{
  background: rgb(245, 132, 132);
}

/* ///////////////////////////////////////////// */

.cart {
  width: calc(100% - 20px);
  border-radius: 10px;
  overflow: hidden;
  padding: 10px;
  margin-bottom: 15px;
  box-shadow: 0 1px 7px 1px rgba(0, 0, 0, 0.1);
  background: white;
}

.cart-row {
  display: flex;
  flex-direction: row;
}

.cart-sub-row {
  padding-left: 15px;
}

.cart-img {
  width: 130px;
  height: 130px;
  border-radius: 10px;
}

.sticky {
  z-index: 99;
  top: -11px;
  position: sticky;
  padding: 10px;
  background: white;
}

.dialog {
  z-index: 99;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
}

.dialog.show {
  display: block;
}

.dialog-main {
  position: absolute;
  top: 50%;
  transform: translateY(-55%);
}

.dialog-title {
  width: 100%;
  text-align: center;
}

.dialog-content {
  height: 410px;
  overflow-x: scroll;
}

.dialog-btn {
  width: calc(100% - 20px);
  padding: 15px 0;
  margin: 3px 10px;
}

.dialog-main.web {
  width: var(--web-page-width);
  left: 50%;
  transform: translate(-50%, -50%);
}

#header-web {
  position: absolute;
  top: 0;
  width: calc(var(--web-page-width) - 10px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10px;
  border-bottom: 5px solid rgb(241, 241, 241);
  background: white;
}

.header-web-item {
  font-size: 17px;
  border: none;
  padding: 6px 10px;
  border-bottom: 3px solid transparent;
  background: white;
}

.header-web-item.active {
  border-bottom: 3px solid var(--primary);
}

.search-name {
  width: calc(100% - 20px);
  padding: 10px 10px;
  border: none;
  border-radius: 10px;
  background: rgb(233, 233, 233);
}

.search-category {
  padding: 7px 10px;
  margin-top: 10px;
  border-radius: 10px;
  border: 1px solid rgb(199, 199, 199);
  background: rgb(230, 230, 230);
}

#account-btn {
  border: 1px solid rgb(197, 197, 197);
  padding: 3px 10px;
  border-radius: 5px;
  background: rgb(224, 224, 224);
}

.search-content {
  box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
}

.dialog-main {
  margin: 0 auto;
  padding: 10px;
  border-radius: 10px;
  background: rgb(252, 252, 252);
}

.dialog-btn {
  font-weight: 600;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 10px;
  background: rgb(226, 226, 226);
}

.dialog-btn.clear {
  background: rgb(255, 147, 147);
}

.accont-content {
  position: relative;
  max-width: 400px;
  margin: 0 auto;
}

.account-input {
  width: calc(100% - 42px);
  padding: 10px 12px;
  margin: 0 10px;
  margin-top: 10px;
  border-radius: 10px;
  border: 1px solid rgb(199, 199, 199);
  background: rgb(240, 240, 240);
}

.button {
  padding: 7px 10px;
  margin: 10px 5px 0;
  border-radius: 10px;
  border: 1px solid rgb(199, 199, 199);
  background: rgb(230, 230, 230);
}

.button.primary {
  color: white;
  background: var(--primary);
}

.button.clear{
  color: white;
  background: rgb(231, 72, 72);
}

.account-title {
  text-align: center;
}

.account-content {
  margin: 10px;
  text-align: center;
}

#header-web-logo {
  user-select: none;
  color: white;
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 10px;
  background: var(--primary);
}

.page-title{
  font-weight: 600;
}

.info-item{
  display: flex;
  justify-content: space-between;
}

.cart-category{
  display: inline-block;
  border-radius: 5px;
  padding: 2px 5px;
  margin: 5px 0;
  background: rgb(215, 215, 215);
}

.cart-count{
  margin-top: 5px;
}

.button.payment{
  width: calc(50% - 10px);
  padding: 10px 0;
  font-size: 14px;
}

.button.payment.active{
  background: rgb(163, 236, 163);
}

.payment-title{
  margin-top: 20px;
  margin-bottom: 5px;
  font-weight: 600;
  text-align: center;
}

.button.order{
  font-size: 17px !important;
}

.message-dialog{
  display: none;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(12, 12, 12, 0.3);
}

.message-dialog.show{
  display: block;
}

.message-dialog.web{
  left: 50%;
  transform: translateX(-50%);
  width: var(--web-page-width);
}

.message-dialog-content{
  position: relative;
  top: 50%;
  transform: translateY(-70%); 
  margin: 0 20px;
  padding: 10px 20px;
  border-radius: 10px;
  background: white;
}

.dialog-right{
  text-align: right;
}

.fa-house, .fa-list, .fa-cart-shopping, .fa-circle-info{
  font-size: 15px;
  margin-bottom: 5px;
}